body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.navbar{
  position: absolute;
width: 1440px;
height: 70px;
left: 0px;
top: 0px;

background: #F5EEC5;
box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.nav-icon{
position: absolute;
width: 80px;
height: 70px;
left: 10px;
top: 0px;

}

.nav-header{
position: absolute;
width: 80px;
height: 100px;
left: 100px;
top: -4px;
color: #3E78DA;

}

.home-btn{
  display: grid;
  position: absolute;
  width: 116px;
  height: 40px;
  left: 1170px;
  top: 10px;
  
  background: #A9BFE1;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  place-items: center;
  }

.sign-btn{
display: grid;
position: absolute;
width: 116px;
height: 40px;
left: 1300px;
top: 10px;

background: #A9BFE1;
box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
place-items: center;

}

.sign-btn:hover, .home-btn:hover  {
  background-color:#3E78DA;
  transition: 0.3s;
}
